export default {
  name: "App",
  data() {
    return {
      trsnsname: 'turn-on',
    };
  },
  methods: {
    clickLink() {
      this.trsnsname = "turn-on"
    }
  },
  mounted() {
    var _this = this;
    window.addEventListener(
      "popstate",
      function(e) {
        _this.trsnsname = "turn-off"
      },
      false
    );
  }
};



